(function() {
  var TalalaUtils;

  TalalaUtils = {
    Global: {
      setHeight: function(elem) {
        var height;
        height = window.innerHeight;
        $(elem).height(height);
        return false;
      },
      setHeightSlider: function() {
        var height;
        height = window.innerHeight;
        if (window.innerHeight >= 800) {
          $("#main-page").height(height);
        }
        if (window.innerHeight <= 660) {
          $("#main-page").height(height);
        }
        return false;
      },
      setWidthBody: function() {
        var width;
        width = window.innerWidth;
        $(".page-info").css({
          "right": "-" + width + "px",
          "display": "none"
        });
        return false;
      },
      goTo: function(elementID) {
        $("html, body").animate({
          scrollTop: $("#" + elementID).offset().top
        }, 1000);
        return false;
      },
      sliderControl: function(direction, control, option) {
        if (option === 'normal') {
          $("#talala_slider_container").animate({
            'left': direction + (control.width * control.position) + "px"
          }, 0);
        } else {
          $("#talala_slider_container").animate({
            'left': direction + (control.width * control.position) + "px"
          }, 2000, "easeOutBounce");
        }
        return false;
      }
    }
  };

  window.TalalaUtils = TalalaUtils;

}).call(this);
